* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  /* background: #f2f2f2; */
  margin: 0 10px;
  background:#000;
}

.audio-player {
  background:#000;
}

.inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* audio info */
.audio-info {
  display: flex;
  gap: 20px;
}

.text {
  color: #f50;
}

.title {
  color:#fff;
  font-weight: 100;
  font-size: 32px;
  margin-bottom: 0;
  padding:10px;
  line-height: 37px;
  background-color: rgba(0,0,0,.8);
}

/* audio image */
.icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.audio-image {
  width: 300px;
  height: 300px;
  background:transparent; 
}

.audio-icon {
  font-size: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  color:#fff;
  width: 80px;
  border-radius: 50%;
  background: #00f;
}

.audio-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  vertical-align: middle;
}

/* controls */
.controls-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.controls button, .volume button {
  border: none;
  background-color: transparent;
  margin-right: 8px;
  color:#00f;
  cursor: pointer;
}

button svg {
  font-size: 20px;
}

.volume {
  display: flex;
  align-items: center;
}

.volume button {
  margin: 0;
}

.progress {
  display: flex;
  align-items: center;
  gap: 10px;
  width: 100%;
}

/* time */
.time {
  color: #00f;
  font-size: 13px;
  line-height: 46px;
}

.time.current {
  color: #f50;
}
 
.top__bar p {
  color:#3fb400;
  font-size:2rem;
  text-transform:uppercase;
  text-align: center;
}

.top__bar p {
  margin: 0;
}

