img{
    border-radius:50%;
    border:1px solid green;
}
.container{
    position: relative;
    display: flex;
    padding: 80px 40px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 80px;
}
.card{
    position: relative;
    width: 350px;
    height: 180px;
    background: #333;
    transition:all .5s;
}
.card:hover{
    height:510px;
}
.card .lines{
    position: absolute;
    inset: 0;
    background: #000;
    overflow: hidden;
}
.card .lines::before{
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 600px;
    height: 120px;
    background:linear-gradient(transparent,#45f3ff,#45f3ff,#45f3ff,transparent);
    animation:animate 4s linear infinite;
}
.card:hover .lines::before{
    animation-play-state: running;
}
@keyframes animate{
    0%{
        transform: translate(-50%,-50%) rotate(0deg);
    }
    100%{
        transform: translate(-50%,-50%) rotate(360deg);
    }
}
.card .lines::after{
    content:'';
    position: absolute;
    inset:3px;
    background: #292929;
}
.card .imgBx{
    position: absolute;
    top:-60px;
    left: 50%;
    width: 150px;
    height: 150px;
    transform:translateX(-50%);
    background: #000;
    transition: all .5s;
    z-index:10;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card:hover .imgBx{
    top: 25px;
    width: 250px;
    height: 250px;
}
.card .imgBx:before{
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 150px;
    transform: translate(-50%,-50%);
    background: linear-gradient(transparent,#ff3c7b,#ff3c7b,#ff3c7b,transparent);
    animation:animate2 6s linear infinite;
    animation-play-state: paused;
}
.card:hover .imgBx::before{
    animation-play-state: running;
}
@keyframes animate2{
    0%{
        transform: translate(-50%,-50%) rotate(360deg);
    }
    100%{
        transform: translate(-50%,-50%) rotate(0deg);
    }
}
.card .imgBx::after{
    content: '';
    position: absolute;
    inset: 3px;
    background:#292929;
}
.card .imgBx img{
    width: 100px;
    position: absolute;
    z-index: 1;
    filter:invert(1);
    opacity: .5s;
    transition:.5s;
}
.card:hover .imgBx img{
  opacity: 1;
}
.card .content{
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    overflow: hidden;
}
.card .content .details{
padding:30px 20px;
text-align: center;
width: 100%;
transition: .5s;
transform:translateY(185px);
}
.card:hover .content .details{
transform:translateY(10px);
}
.card .content .details h2{
font-size: 1.5rem;

font-weight: 500;
color: #45f3ff;
line-height:1.2em;
}
.card .content .details p{
color: #fff;
opacity: 0;
transition: .5s;
}
.card:hover .content .details p{
opacity: 1;
}

.card .content .details a{
display: inline-block;
padding: 8px 15px;
background: #45f3ff;
color: #292929;
margin-top:10px;
font-weight: 500;
text-decoration:none;
opacity:0;
transition: .5s;
}
.card:hover .content .details a,
.card:hover .content .details p{
opacity: 1;
}